import React, { Component } from 'react';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter, showAddAccountAlert } from '../../../../Services/Utils/utils';
import CheckBoxField from '../Elements/CheckboxField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { addUserAccount, createFeedAction, fetchConnectedAccounts } from '../../../../Services/Actions/addFeedAction';
import { connect } from 'react-redux';
import PlaceDropDown from './components/PlaceDropDown';
import InfoSection from '../Elements/InfoSection';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import GoogleTab from './components/GoogleTab';
import { toast } from 'react-toastify';

const channel = new BroadcastChannel('connect');

class GoogleFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            feedText: '',
            placeName: null,
            placeId: null,
            moderation: false,
            error: false,
            loading: false,
            connetedAccount: 0,
            accountChild: 0,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype,
            otherAccount: false
        }
    }

    onFieldTextChange = (text, id, placeName) => this.setState({ feedText: text, placeId: id, placeName: placeName, error: false })

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onotherAccount = (value) => this.setState({ otherAccount: value });
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    onAccountChildChange = (value) => this.setState({ accountChild: value })

    addGoogleAccount = () => {
        const { networkData } = this.props;
        showAddAccountAlert(networkData);
    }


    createFeed = () => {
        const { feedText, placeId, connetedAccount, accountChild, placeName, moderation, selectedFilterId } = this.state;
        const { collection, networkId, history } = this.props;

        var obj = {
            collection: collection != null ? collection.value : "",
            networkId: networkId,
            filter: selectedFilterId,
            moderation: moderation ? 1 : 0,
            connetedAccount: 0,
            account: 0
        }
        if (selectedFilterId == 33) {
            obj['field33'] = feedText;
            obj['field33placeId'] = placeId
            obj['field33placeName'] = placeName
        } else if (selectedFilterId == 29) {
            obj['field33'] = '';
            obj['field33placeId'] = '';
            obj['field33placeName'] = '';
            obj['connetedAccount'] = connetedAccount;
            obj['account'] = accountChild;
        }

        this.setState({ error: false, topicError: false, loading: true })
        createFeedAction(obj, networkId, () => {
            this.setState({ loading: false });
            this.props.closeAddFeed();
            history('/assets');
        });
    }

    onSubmit = () => {
        const { feedText, selectedFilterTab, accountChild, connetedAccount } = this.state;

        if (selectedFilterTab == 'Places' && feedText == '') {
            this.setState({ error: true });
        } else {
            if (selectedFilterTab == 'My Places' && (connetedAccount == 0 || accountChild == 0)) {
                toast.error('Need to connect google account', { autoClose: 3000 });
                return;
            }

            this.createFeed();
        }
    }

    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId } = response;

        addUserAccount(networkId, data, () => this.props.getConnectedAccount(networkId));

    }

    componentDidMount() {
        channel.addEventListener("message", this.readAuthenticationCode);
    }

    componentWillUnmount() {
        channel.removeEventListener("message", this.readAuthenticationCode);
    }
    feedIcon = (iconId) => {
        switch (iconId) {
            case 33: // Place
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M12 23.7279L5.63604 17.364C2.12132 13.8492 2.12132 8.15076 5.63604 4.63604C9.15076 1.12132 14.8492 1.12132 18.364 4.63604C21.8787 8.15076 21.8787 13.8492 18.364 17.364L12 23.7279ZM16.9497 15.9497C19.6834 13.2161 19.6834 8.78392 16.9497 6.05025C14.2161 3.31658 9.78392 3.31658 7.05025 6.05025C4.31658 8.78392 4.31658 13.2161 7.05025 15.9497L12 20.8995L16.9497 15.9497ZM12 13C10.8954 13 10 12.1046 10 11C10 9.89543 10.8954 9 12 9C13.1046 9 14 9.89543 14 11C14 12.1046 13.1046 13 12 13Z" fill="currentColor"></path>
            </svg>
                break;
            case 29: // My Place
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M17.0839 15.812C19.6827 13.0691 19.6379 8.73845 16.9497 6.05025C14.2161 3.31658 9.78392 3.31658 7.05025 6.05025C4.36205 8.73845 4.31734 13.0691 6.91612 15.812C7.97763 14.1228 9.8577 13 12 13C14.1423 13 16.0224 14.1228 17.0839 15.812ZM12 23.7279L5.63604 17.364C2.12132 13.8492 2.12132 8.15076 5.63604 4.63604C9.15076 1.12132 14.8492 1.12132 18.364 4.63604C21.8787 8.15076 21.8787 13.8492 18.364 17.364L12 23.7279ZM12 12C10.3431 12 9 10.6569 9 9C9 7.34315 10.3431 6 12 6C13.6569 6 15 7.34315 15 9C15 10.6569 13.6569 12 12 12Z" fill="currentColor"></path></svg>
                break;
            default:
                break;
        }
    }
    render() {
        const { filterTabs, connectedAccountList } = this.props;
        const { connetedAccount, otherAccount, accountChild, moderation, error, loading, selectedFilterTab } = this.state;
        var childAcc = connectedAccountList && connectedAccountList[0] && connectedAccountList[0].ConnectedAccounts && connectedAccountList[0].ConnectedAccounts.child && connectedAccountList[0].ConnectedAccounts.child.length ? connectedAccountList[0].ConnectedAccounts.child : ''

        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        {filterTabs && Object.keys(filterTabs).length > 1 ?
                        <div className="w-100">
                            <h6 class="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                        {filterTabs && filterTabs.map((filterTab) => {
                                            const { feedName, feedtype, filter } = filterTab;
                                            return <Nav.Item className="p-1" onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                <Nav.Link eventKey={feedtype}>
                                                    <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                        {this.feedIcon(filter)}
                                                    </span>
                                                    {capitalizeFirstLetter(feedtype)}
                                                </Nav.Link>
                                            </Nav.Item>
                                        })
                                        }
                                    </Nav>
                                </div>
                            </div>
                        </div>
                        :null}
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">
                                        <Tab.Content>
                                            <Tab.Pane eventKey={"Places"}>
                                                <div className="mb-2 mb-xl-3">
                                                    <div className={`${error ? 'is-invalid': ''}`}>
                                                        <PlaceDropDown onLatlanChange={this.onLatlanChange} onFieldTextChange={this.onFieldTextChange} error={error}/>
                                                    </div>
                                                    {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                </div>
                                                <InfoSection text={'Initially the latest 5 user reviews of Place will be aggregated after which all the upcoming reviews will be aggregated.'} />
                                            </Tab.Pane>
                                            <Tab.Pane eventKey={"My Places"}>
                                                {connectedAccountList && connectedAccountList.length > 0 ?
                                                    <GoogleTab
                                                        onAddAccount={this.addGoogleAccount}
                                                        connectedAcc={connectedAccountList}
                                                        otherAccount={otherAccount}
                                                        connetedAccount={connetedAccount}
                                                        accountChild={accountChild}
                                                        onotherAccount={this.onotherAccount}
                                                        onAccountChildChange={this.onAccountChildChange}
                                                        onConnectedAccountChange={this.onConnectedAccountChange}
                                                        childAcc={childAcc}
                                                    />

                                                    :
                                                    <div className="otherSection py-2 mb-2">
                                                        <button type="button" className="btn btn-outline btn-lg align-items-center d-flex signInWithBrand m-auto">
                                                            <span className="h-40px w-40px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-light">
                                                                <img src={baseUrl("media/icons/social-icons/google.svg")} height={43} width={43} alt="facebook" />
                                                            </span>
                                                            <div className="overflow-hidden signIn_txt px-5">
                                                                <span className="text-gray-800 mb-0 fs-8 fw-medium text-ellipsis me-2">Connect Google</span>
                                                            </div>
                                                        </button>
                                                    </div>
                                                }
                                                {/* {!otherAccount ? <div className="pb-2">
                                                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                        <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                            <span className="form-check-label">Manually approve posts before making them public?</span>
                                                        </CheckBoxField>
                                                    </label>
                                                </div> : null} */}

                                                <InfoSection text={'Initially latest 50 user reviews will be aggregated after which all the upcoming reviews will be aggregated.'} />
                                            </Tab.Pane>
                                        </Tab.Content>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container >
                <SubmitFeedRequest hideBtn={otherAccount} supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount,
        connectedAccountList: state.addFeed.connectedAcc
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getConnectedAccount: (id) => dispatch(fetchConnectedAccounts(id)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(GoogleFeed)