import React, { Component } from 'react';
// import { AUTOCOMPLETE } from "../../../../actions/manageFeedsAction";
import AccountPicker from '../../Elements/AccountPicker';
import { debounce } from '../../../../../Services/Utils/utils';
import InputField from '../../Elements/InputField';
import CheckBoxField from '../../Elements/CheckboxField';
import InfoSection from '../../Elements/InfoSection';
import { autoComplete } from '../../../../../Services/Actions/addFeedAction';

class TwitterTabs extends Component {
    state = {
        showList: false,
        lists: [],
    }

    autoComplete = debounce((e) => {
        autoComplete({
            networkId: 1,
            handle: e.target.value,
        }).then(res => {
            const { data } = res
            if (data.response_code == 200) {
                this.setState({ lists: data.responseData.data, showList: true })
            }
            else this.setState({ lists: [], showList: false })
        })
    })

    handleAutoComplete = (e) => {
        this.props.onChangeHandler(e);
        this.autoComplete(e);
    }

    onSelectList = (item) => {

        const { onListNameChange, onListIdChange } = this.props
        onListNameChange(item.name);
        onListIdChange(item.id, item.owner_id);
        this.setState({ showList: false })
    }
   
    render() {
        const { lists, showList } = this.state
        const { hashtag, handle, networkId, onConnectedAccountChange, userName, showListError, listNameError, listName, feedIcon, advanced, tab, error, handleError, mentionError, advanceError, multiPhoto, listname, excludeRetweets, onChangeHandler, excludeReplies, onCheckHandler, mentions, favorites, favoriteError, connectedAccountList, filter } = this.props;

        return (
            <>
                <div className="mb-2 mb-xl-3">
                    <div className="">
                        {tab != 'list' ? 
                        <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                            <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                {feedIcon(filter)}
                            </span>
                            <InputField name={tab} className={`form-control form-control-lg fw-bolder px-9 ${(tab == "hashtag" && error)||(handleError && tab == "handle")||(mentionError && tab == "mentions")||(favoriteError && tab == "favorites")||(advanceError && tab == "advanced")?'is-invalid':''}`} placeholder={tab !== "advanced" ? `Enter ${tab !== 'favorites' ? tab : 'the twitter handle of the user to import Liked Posts from.'}` : 'Query'} value={tab == "hashtag" ? hashtag : tab == "handle" ? handle : tab == "mentions" ? mentions : tab == "advanced" ? advanced : favorites} onChangeHandler={onChangeHandler} /> 
                            {(tab == "hashtag" && error)||(handleError && tab == "handle")||(mentionError && tab == "mentions")||(favoriteError && tab == "favorites")||(advanceError && tab == "advanced")? <span className="invalid-feedback">This field is required.</span> : null}
                        </div>
                        : 
                        
                        <div className="row">
                            <div className="col-sm-6">
                                <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                    <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                        {feedIcon(9)}
                                    </span>
                                    <InputField name={"userName"} placeholder="Enter Keyword" className={`form-control form-control-lg fw-bolder px-9 ${showListError?'is-invalid':''}`} value={userName} onChangeHandler={(e) => this.handleAutoComplete(e)} autoComplete={true} />
                                    {showList ? lists && lists.length > 0 ? lists.map((item, index) => (
                                        <li key={item.id} onClick={() => this.onSelectList(item)}>
                                            <div className="content-section">
                                                {item.name}
                                                <p>https://twitter.com/{item.name}</p>
                                            </div>
                                        </li>
                                    )) : null : null}
                                    {showListError && <span className="invalid-feedback">This field is required.</span>}
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                    <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                        {feedIcon(7)}
                                    </span>
                                    <InputField name={"listName"} placeholder="Enter Username" className={`form-control form-control-lg fw-bolder px-9 ${ listNameError ?'is-invalid':''}`} value={listName} onChangeHandler={onChangeHandler} autoComplete={true} />
                                    {listNameError && <span className="invalid-feedback">This field is required.</span>}
                                </div>
                            </div>
                        </div>}
                        {(tab == "hashtag" && error)||(handleError && tab == "handle")||(mentionError && tab == "mentions")||(favoriteError && tab == "favorites")||(advanceError && tab == "advanced") ? <span className="invalid-feedback">This field is required</span> : null}

                        {tab == "advanced" ? <p className="fs-8 fw-medium text-gray-700 mb-1">Use the table below to construct your query.</p> : null}
                        {
                            tab == "advanced" ? <div className="card mb-5">
                                <div className='card-body'>
                                    <div className="table-responsive" style={{height:200}}>
                                        <table className="table table-light mb-0">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: "20%" }}><strong>Filter By:</strong></th>
                                                    <th><strong>Example</strong></th>
                                                    <th><strong>Targets Tweets:</strong></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <span className="svg-icon svg-icon-5 text-gray-800">
                                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                                                                    <path d="M13 6V21H11V6H5V4H19V6H13Z" fill="currentColor"></path>
                                                                </svg>
                                                            </span>
                                                            Keywords
                                                        </div>
                                                    </td>
                                                    <td>grapes banana</td>
                                                    <td>containing both "grapes" and "banana".</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>"good time"</td>
                                                    <td>containing the exact phrase "good time"</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>die OR alive</td>
                                                    <td>containing either "die" or "alive" (or both)</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>food -drink</td>
                                                    <td>containing "food" but not "drink"</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>#soul</td>
                                                    <td>containing the hashtag "#soul"</td>
                                                </tr>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <span className="svg-icon svg-icon-5 text-gray-800">
                                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M4 22C4 17.5817 7.58172 14 12 14C16.4183 14 20 17.5817 20 22H18C18 18.6863 15.3137 16 12 16C8.68629 16 6 18.6863 6 22H4ZM12 13C8.685 13 6 10.315 6 7C6 3.685 8.685 1 12 1C15.315 1 18 3.685 18 7C18 10.315 15.315 13 12 13ZM12 11C14.21 11 16 9.21 16 7C16 4.79 14.21 3 12 3C9.79 3 8 4.79 8 7C8 9.21 9.79 11 12 11Z" fill="currentColor"></path></svg>
                                                            </span>
                                                            User
                                                        </div>
                                                    </td>
                                                    <td>from:taggbox</td>
                                                    <td>sent from the user @taggbox</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>to:taggbox</td>
                                                    <td>sent to the user @taggbox</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>@taggbox</td>
                                                    <td>mentioning @taggbox</td>
                                                </tr>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <span className="svg-icon svg-icon-5 text-gray-800">
                                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M17.6572 14.8282L16.2429 13.414L17.6572 11.9998C19.2193 10.4377 19.2193 7.90506 17.6572 6.34296C16.0951 4.78086 13.5624 4.78086 12.0003 6.34296L10.5861 7.75717L9.17188 6.34296L10.5861 4.92875C12.9292 2.5856 16.7282 2.5856 19.0714 4.92875C21.4145 7.27189 21.4145 11.0709 19.0714 13.414L17.6572 14.8282ZM14.8287 17.6567L13.4145 19.0709C11.0714 21.414 7.27238 21.414 4.92923 19.0709C2.58609 16.7277 2.58609 12.9287 4.92923 10.5856L6.34345 9.17139L7.75766 10.5856L6.34345 11.9998C4.78135 13.5619 4.78135 16.0946 6.34345 17.6567C7.90555 19.2188 10.4382 19.2188 12.0003 17.6567L13.4145 16.2425L14.8287 17.6567ZM14.8287 7.75717L16.2429 9.17139L9.17188 16.2425L7.75766 14.8282L14.8287 7.75717Z" fill="currentColor"></path></svg>
                                                            </span>
                                                            Content
                                                        </div>
                                                    </td>
                                                    <td>funny filter:links</td>
                                                    <td>containing " funny" and with a URL.</td>
                                                </tr>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <span className="svg-icon svg-icon-5 text-gray-800">
                                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M9 1V3H15V1H17V3H21C21.5523 3 22 3.44772 22 4V20C22 20.5523 21.5523 21 21 21H3C2.44772 21 2 20.5523 2 20V4C2 3.44772 2.44772 3 3 3H7V1H9ZM20 11H4V19H20V11ZM11 13V17H6V13H11ZM7 5H4V9H20V5H17V7H15V5H9V7H7V5Z" fill="currentColor"></path></svg>
                                                            </span>
                                                            Dates
                                                        </div>
                                                    </td>
                                                    <td>champion&amp;start_time=2023-05-11T05:00:01.340Z</td>
                                                    <td>containing "champion" and sent since date "2023-05-11T05:00:01.340Z" (year-month-day).</td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>champion&amp;end_time=2023-05-17T05:00:01.340Z</td>
                                                    <td>containing "champion" and sent before the date "2023-05-17T05:00:01.340Z".</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div> : null
                        }
                    </div>
                </div>
                <div className="pb-2">
                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={multiPhoto} onChangeHandler={onCheckHandler} name={"multiPhoto"}>
                            <span className="form-check-label">Add Multiple Photos</span>
                        </CheckBoxField>
                    </label>
                    {tab != 'list' && tab != "advanced" ? <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={excludeRetweets} onChangeHandler={onCheckHandler} name={"excludeRetweets"}>
                            <span className="form-check-label">Exclude Retweets</span>
                        </CheckBoxField>
                    </label> : null}
                    {tab != 'favorites' && tab != 'list' ? <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={excludeReplies} onChangeHandler={onCheckHandler} name={"excludeReplies"}>
                            <span className="form-check-label">Exclude Replies</span>
                        </CheckBoxField>
                    </label> : null}
                </div>
                {(connectedAccountList && connectedAccountList.length > 0) && <AccountPicker connectedAcc={connectedAccountList} networkId={networkId} onConnectedAccountChange={onConnectedAccountChange} />}
                {tab != 'list' && tab != "advanced" && tab != "favorites" ?
                    <InfoSection text={tab == 'hashtag' ? `Only 10 tweets will be aggregated from the last 7 days' data, after which all the upcoming tweets will be aggregated.` : tab == 'handle' ? `Initially, 10 tweets will be aggregated from the Twitter handle, after which all the upcoming tweets will be aggregated.` : tab == 'mentions' ? `Only 10 mentions from the last 7 days will be aggregated, after which all the upcoming tweets will be aggregated.` : ``} />
                    : null
                }
            </>
        )
    }
}

export default TwitterTabs

