import React, { Component } from 'react';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter } from '../../../../Services/Utils/utils';
import InputField from '../Elements/InputField';
import CheckBoxField from '../Elements/CheckboxField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { createFeedAction } from '../../../../Services/Actions/addFeedAction';



class Pinterest extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userBoard: "",
            handle: "",
            moderation: false,
            error: false,
            errorMsg: "This field is required",
            handleError: false,
            loading: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, handleError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }


    onSubmit = () => {
        const { userBoard, handle, moderation, selectedFilterTab, selectedFilterId } = this.state;
        const { collection, networkId } = this.props;

        if (selectedFilterTab == 'Handle' && handle == '') {
            this.setState({ handleError: true });
        } else if (selectedFilterTab == 'User Board' && userBoard == '') {
            this.setState({ error: true, errorMsg: "This field is required" });
        } else {

            var obj = {
                collection: collection != null ? collection.value : "",
                networkId: networkId,
                filter: selectedFilterId,
                moderation: moderation ? 1 : 0,
                field12boardurl: '',
                field1: ''
            }

            if (selectedFilterId == 1) {
                obj['field1'] = handle
            } else if (selectedFilterId == 12) {
                    obj['field12boardurl'] = userBoard
        
                    if (userBoard.includes('pinterest.com')) {
                        let url = userBoard.split("https://in.pinterest.com/")[1].replace("/", "");
                        if (url) {
                            obj['field1'] = url;
                        } else {
                            this.setState({ errorMsg: 'This field must contain a url.', error: true})
                            return false
                        }
                    } else {
                        this.setState({ errorMsg: 'This field must contain a url.', error: true })
                        return false
                    }
            }

            this.setState({ error: false, handleError: false, loading: true });

            createFeedAction(obj,networkId,() =>{
                this.setState({ loading: false, isAuthRequired: true });
                this.props.closeAddFeed()
                this.props.history('/assets');
            });

        }
    }
    feedIcon = (iconId) => {
        switch (iconId) {
            case 1: // @
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M20 12C20 7.58172 16.4183 4 12 4C7.58172 4 4 7.58172 4 12C4 16.4183 7.58172 20 12 20C13.6418 20 15.1681 19.5054 16.4381 18.6571L17.5476 20.3214C15.9602 21.3818 14.0523 22 12 22C6.47715 22 2 17.5228 2 12C2 6.47715 6.47715 2 12 2C17.5228 2 22 6.47715 22 12V13.5C22 15.433 20.433 17 18.5 17C17.2958 17 16.2336 16.3918 15.6038 15.4659C14.6942 16.4115 13.4158 17 12 17C9.23858 17 7 14.7614 7 12C7 9.23858 9.23858 7 12 7C13.1258 7 14.1647 7.37209 15.0005 8H17V13.5C17 14.3284 17.6716 15 18.5 15C19.3284 15 20 14.3284 20 13.5V12ZM12 9C10.3431 9 9 10.3431 9 12C9 13.6569 10.3431 15 12 15C13.6569 15 15 13.6569 15 12C15 10.3431 13.6569 9 12 9Z" fill="currentColor"></path></svg>
                break;
            case 12: // Link
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M18.3643 15.5353L16.95 14.1211L18.3643 12.7069C20.3169 10.7543 20.3169 7.58847 18.3643 5.63585C16.4116 3.68323 13.2458 3.68323 11.2932 5.63585L9.87898 7.05007L8.46477 5.63585L9.87898 4.22164C12.6127 1.48797 17.0448 1.48797 19.7785 4.22164C22.5121 6.95531 22.5121 11.3875 19.7785 14.1211L18.3643 15.5353ZM15.5358 18.3638L14.1216 19.778C11.388 22.5117 6.9558 22.5117 4.22213 19.778C1.48846 17.0443 1.48846 12.6122 4.22213 9.87849L5.63634 8.46428L7.05055 9.87849L5.63634 11.2927C3.68372 13.2453 3.68372 16.4112 5.63634 18.3638C7.58896 20.3164 10.7548 20.3164 12.7074 18.3638L14.1216 16.9496L15.5358 18.3638ZM14.8287 7.75717L16.2429 9.17139L9.17187 16.2425L7.75766 14.8282L14.8287 7.75717Z" fill="currentColor"></path></svg>
                break;
            default:
                break;
        }
    }
    render() {
        const { filterTabs } = this.props;
        const { userBoard, handle, moderation, handleError, error, loading, selectedFilterTab, errorMsg } = this.state
        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">

                                        <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                            {
                                                filterTabs && filterTabs.map((filterTab) => {
                                                    const { feedName, feedtype, filter } = filterTab;
                                                    return <Nav.Item className="p-1" onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                        <Nav.Link eventKey={feedtype}>
                                                            <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                                {this.feedIcon(filter)}
                                                            </span>
                                                            {capitalizeFirstLetter(feedtype)}
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                })
                                            }
                                        </Nav>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">
                                        <Tab.Content>
                                            <Tab.Pane eventKey={"Handle"}>
                                                <div className="mb-2 mb-xl-3">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(1)}
                                                        </span>
                                                        <InputField className={`form-control form-control-lg fw-bolder px-9 ${handleError? 'is-invalid':''}`} name={"handle"} placeholder="Enter user handle" value={handle} onChangeHandler={this.onChangeHandler} />
                                                        {handleError ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                {/* <div className="pb-2">
                                                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                        <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                            <span className="form-check-label">Manually approve posts before making them public?</span>
                                                        </CheckBoxField>
                                                    </label>
                                                </div> */}
                                            </Tab.Pane>
                                            <Tab.Pane eventKey={"User Board"}>
                                                <div className="mb-2 mb-xl-3">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(12)}
                                                        </span>
                                                        <InputField className={`form-control form-control-lg fw-bolder px-9 ${error? 'is-invalid':''}`} name={"userBoard"} placeholder="https://in.pinterest.com/taggbox/taggbox-social-blog/" value={userBoard} onChangeHandler={this.onChangeHandler} />
                                                        {error ? <span className="invalid-feedback">{errorMsg}</span> : null}
                                                    </div>
                                                </div>
                                                {/* <div className="pb-2">
                                                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                        <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                            <span className="form-check-label">Manually approve posts before making them public?</span>
                                                        </CheckBoxField>
                                                    </label>
                                                </div> */}
                                            </Tab.Pane>
                                        </Tab.Content>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}

export default Pinterest