import React, { Component } from 'react';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter } from '../../../../Services/Utils/utils';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { createFeed, createFeedAction, createPost } from '../../../../Services/Actions/addFeedAction';
import { toast } from 'react-toastify';
import YoutubeTabs from './components/YoutubeTabs';



class YoutubeFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            textField: "",
            moderation: false,
            keyword: "",
            error: false,
            shortError:false,
            playListError:false,
            keywordError:false,
            locationError:false,
            locationKeywordError:false,
            channelId: "",
            latlng: "",
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    onTextFieldChange = (value) => this.setState({ textField: value,error:false,shortError:false,keywordError:false,playListError:false,keywordError:false })
    onManuallyApproveChange = (value) => this.setState({ moderation: value })
    onKeywordChange = (value) => this.setState({ keyword: value,locationKeywordError:false })
    onChannelIdChange = (value) => this.setState({ channelId: value })
    onLatlanChange = (value) => this.setState({ latlng: value,locationError:false })

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, handleError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }


    clearValues = (filter,feedtype) => {
        this.onTextFieldChange('')
        this.onChannelIdChange("");
        this.changeTab(filter, feedtype);
    }

    onSubmit = () => {
        const { textField, channelId, moderation,keyword,latlng,selectedFilterId } = this.state;
        const { collection, networkId } = this.props;
        if (selectedFilterId == 1 && textField == ""){
            this.setState({ error: true})
        }else if(selectedFilterId == 75 && textField == ''){
            this.setState({ shortError: true})
        }else if(selectedFilterId == 11 && textField == ''){
            this.setState({ playListError: true})
        }else if(selectedFilterId == 4 && textField == ''){
            this.setState({ keywordError: true})
        }else if(selectedFilterId == 32 && textField == ''){
            this.setState({ locationError: true})
        }else if(selectedFilterId == 32 && keyword == ''){
            this.setState({ locationKeywordError: true})
        }else {
            var obj = {
                collection: collection ? collection.value: '',
                networkId: networkId,
                filter: selectedFilterId,
                moderation: moderation ? 1 : 0,
                feedText: textField,
                channelId: channelId ? channelId : "",
            }

            if(selectedFilterId == 4 ){
                obj['channelId'] = "";
            } else if (selectedFilterId == 32){
                obj['channelId'] = "";
                obj = { ...obj, keywords: keyword, latlng };
            } 
                      
           this.setState({ error: false, shortError:false,keywordError:false,playListError:false,keywordError:false, loading: true })
           createFeedAction(obj,networkId,() =>{
            this.setState({ loading: false });
            this.props.closeAddFeed();
            this.props.history('/assets');
        });
            
        }
    }

    feedIcon = (iconId) => {
        switch (iconId) {
            case 1: // Link
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M18.3643 15.5353L16.95 14.1211L18.3643 12.7069C20.3169 10.7543 20.3169 7.58847 18.3643 5.63585C16.4116 3.68323 13.2458 3.68323 11.2932 5.63585L9.87898 7.05007L8.46477 5.63585L9.87898 4.22164C12.6127 1.48797 17.0448 1.48797 19.7785 4.22164C22.5121 6.95531 22.5121 11.3875 19.7785 14.1211L18.3643 15.5353ZM15.5358 18.3638L14.1216 19.778C11.388 22.5117 6.9558 22.5117 4.22213 19.778C1.48846 17.0443 1.48846 12.6122 4.22213 9.87849L5.63634 8.46428L7.05055 9.87849L5.63634 11.2927C3.68372 13.2453 3.68372 16.4112 5.63634 18.3638C7.58896 20.3164 10.7548 20.3164 12.7074 18.3638L14.1216 16.9496L15.5358 18.3638ZM14.8287 7.75717L16.2429 9.17139L9.17187 16.2425L7.75766 14.8282L14.8287 7.75717Z" fill="currentColor"></path></svg>
                break;
            case 75: // Shorts
                return <svg xmlns="http://www.w3.org/2000/svg" width="15.259" height="19.948" viewBox="0 0 15.259 19.948">
                    <path id="Path_3" data-name="Path 3" d="M.549,17.631a4.369,4.369,0,0,1,1.49-5.75,3.972,3.972,0,0,1-1.932-2.7A4.248,4.248,0,0,1,2.164,4.365c1.65-.986,3.338-1.95,4.972-2.883q.815-.467,1.633-.935A4.192,4.192,0,0,1,10.838,0a4.48,4.48,0,0,1,2.849,1.037A4.274,4.274,0,0,1,15.21,3.763a4.374,4.374,0,0,1-2.037,4.355,4.369,4.369,0,0,1,.471,7.115l-.007.006a7.809,7.809,0,0,1-1.117.718c-.1.058-.205.115-.306.175-1.575.917-3.647,2.12-5.711,3.288l-.014.007a4.384,4.384,0,0,1-5.941-1.8ZM9.513,1.85q-.815.468-1.634.935C6.252,3.713,4.57,4.674,2.935,5.651A2.742,2.742,0,0,0,1.586,8.93a2.552,2.552,0,0,0,1.373,1.754c.282.158.571.325.964.558L5,11.883l-1.079.647c-.375.226-.717.428-1.088.621a2.884,2.884,0,0,0,1.588,5.3,2.817,2.817,0,0,0,1.345-.339c2.054-1.162,4.118-2.36,5.686-3.274.107-.063.217-.124.326-.185a6.555,6.555,0,0,0,.9-.573,2.872,2.872,0,0,0,1.06-2.485,2.928,2.928,0,0,0-1.528-2.3c-.163-.093-.316-.184-.478-.282-.133-.081-.272-.165-.419-.248l-1.09-.629,1.072-.66c.3-.184.606-.365.939-.553l.012-.007a2.87,2.87,0,0,0,1.471-2.938l0-.013a2.773,2.773,0,0,0-.995-1.774A2.977,2.977,0,0,0,10.838,1.5,2.641,2.641,0,0,0,9.513,1.85ZM5.586,6.735,11.248,10,5.586,13.28ZM6.8,11.171,8.819,10,6.8,8.839Z"  fill="currentColor"/>
                </svg>              
                break;
            case 11: // Play List
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M22 18V20H2V18H22ZM2 3.5L10 8.5L2 13.5V3.5ZM22 11V13H12V11H22ZM4 7.1085V9.8915L6.22641 8.5L4 7.1085ZM22 4V6H12V4H22Z" fill="currentColor"></path></svg>
                break;
            case 4: // Keyword
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M13 6V21H11V6H5V4H19V6H13Z" fill="currentColor"></path></svg>
                break;
            case 32: // Location
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M12 23.7279L5.63604 17.364C2.12132 13.8492 2.12132 8.15076 5.63604 4.63604C9.15076 1.12132 14.8492 1.12132 18.364 4.63604C21.8787 8.15076 21.8787 13.8492 18.364 17.364L12 23.7279ZM16.9497 15.9497C19.6834 13.2161 19.6834 8.78392 16.9497 6.05025C14.2161 3.31658 9.78392 3.31658 7.05025 6.05025C4.31658 8.78392 4.31658 13.2161 7.05025 15.9497L12 20.8995L16.9497 15.9497ZM12 13C10.8954 13 10 12.1046 10 11C10 9.89543 10.8954 9 12 9C13.1046 9 14 9.89543 14 11C14 12.1046 13.1046 13 12 13Z" fill="currentColor"></path></svg>
                break;
            default:
                break;
        }
    }

    render() {
        const { filterTabs } = this.props;
        const { textField, shortError,playListError,keywordError,locationError,locationKeywordError, channelId, moderation, keyword, error, loading, selectedFilterTab } = this.state
        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">

                                        <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                            {
                                                filterTabs && filterTabs.map((filterTab) => {
                                                    const { feedName, feedtype, filter } = filterTab;
                                                    return <Nav.Item className="p-1" onClick={() =>this.clearValues(filter,feedtype) } key={`networkTab${filter}`}>
                                                        <Nav.Link eventKey={feedtype}>
                                                            <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                                {this.feedIcon(filter)}
                                                            </span>
                                                            {capitalizeFirstLetter(feedtype)}
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                })
                                            }
                                        </Nav>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">
                                        <Tab.Content>
                                            {(filterTabs && filterTabs.length > 0) ? filterTabs.map(filter => <Tab.Pane eventKey={filter.feedtype} key={filter.feedtype}>
                                                <YoutubeTabs tab={filter.feedtype} error={error} onLatlanChange={this.onLatlanChange} value={textField} feedIcon={this.feedIcon} onValueChange={this.onTextFieldChange} checkBox={moderation} onCheckBoxChange={this.onCheckHandler} onKeywordChange={this.onKeywordChange} onChannelIdChange={this.onChannelIdChange} channelId={channelId} keyword={keyword} shortError={shortError} playListError={playListError} keywordError={keywordError} locationError={locationError} locationKeywordError={locationKeywordError} filter={filter.filter}/>
                                            </Tab.Pane>
                                            ) : null}
                                        </Tab.Content>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}

export default YoutubeFeed