import { Component } from "react";
import { connect } from 'react-redux';
import { Nav, Tab } from 'react-bootstrap';
import { capitalizeFirstLetter } from "../../../../Services/Utils/utils";
import InputField from "../Elements/InputField";
import CheckBoxField from "../Elements/CheckboxField";
import { createFeedAction } from "../../../../Services/Actions/addFeedAction";
import SubmitFeedRequest from "../Elements/SubmitFeedRequest";

class Airbnb extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pageUrl: '',
            error: false,
            moderation: false,
            loading: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }

    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onSubmit = () => {
        const { pageUrl, moderation } = this.state;
        const { collection, networkId,history } = this.props;

        if (!pageUrl || pageUrl == "") this.setState({ error: true })
        else {

            this.setState({ error: false, loading: true })

            var obj = {
                collection: collection != null ? collection.value : '',
                networkId: networkId,
                filter: 56,
                airbnb_page_review: pageUrl,
                moderation: moderation ? 1 : 0,
            }

            createFeedAction(obj, networkId, () => {
                this.setState({ loading: false });
                this.props.closeAddFeed();
                history('/assets');
            });
        }
    }



    render() {
        const { pageUrl, error, moderation, loading, selectedFilterTab } = this.state;
        const { filterTabs } = this.props;
        return <>
            <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                <div className="addSourceTabs">
                    {filterTabs && Object.keys(filterTabs).length > 1 ?
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Select the type of posts</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <div className="">
                                    <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                        {
                                            filterTabs && filterTabs.map((filterTab) => {
                                                const { feedName, feedtype, filter } = filterTab;
                                                return <Nav.Item className="p-1" onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                    <Nav.Link eventKey={feedtype}>
                                                        <span className="svg-icon">
                                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                                <path d="M17.6572 14.8282L16.2429 13.414L17.6572 11.9998C19.2193 10.4377 19.2193 7.90506 17.6572 6.34296C16.0951 4.78086 13.5624 4.78086 12.0003 6.34296L10.5861 7.75717L9.17188 6.34296L10.5861 4.92875C12.9292 2.5856 16.7282 2.5856 19.0714 4.92875C21.4145 7.27189 21.4145 11.0709 19.0714 13.414L17.6572 14.8282ZM14.8287 17.6567L13.4145 19.0709C11.0714 21.414 7.27238 21.414 4.92923 19.0709C2.58609 16.7277 2.58609 12.9287 4.92923 10.5856L6.34345 9.17139L7.75766 10.5856L6.34345 11.9998C4.78135 13.5619 4.78135 16.0946 6.34345 17.6567C7.90555 19.2188 10.4382 19.2188 12.0003 17.6567L13.4145 16.2425L14.8287 17.6567ZM14.8287 7.75717L16.2429 9.17139L9.17188 16.2425L7.75766 14.8282L14.8287 7.75717Z" fill="currentColor"></path>
                                                            </svg>
                                                        </span>
                                                        {capitalizeFirstLetter(feedtype)}
                                                    </Nav.Link>
                                                </Nav.Item>
                                            })
                                        }
                                    </Nav>
                                </div>
                            </div>
                        </div>
                    </div>
                    : null}
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <Tab.Content>
                                    <Tab.Pane eventKey={"Listing Url"}>
                                        <div className="mb-2 mb-xl-3">
                                            <div className="position-relative w-100">
                                                <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M18.3643 15.5353L16.95 14.1211L18.3643 12.7069C20.3169 10.7543 20.3169 7.58847 18.3643 5.63585C16.4116 3.68323 13.2458 3.68323 11.2932 5.63585L9.87898 7.05007L8.46477 5.63585L9.87898 4.22164C12.6127 1.48797 17.0448 1.48797 19.7785 4.22164C22.5121 6.95531 22.5121 11.3875 19.7785 14.1211L18.3643 15.5353ZM15.5358 18.3638L14.1216 19.778C11.388 22.5117 6.9558 22.5117 4.22213 19.778C1.48846 17.0443 1.48846 12.6122 4.22213 9.87849L5.63634 8.46428L7.05055 9.87849L5.63634 11.2927C3.68372 13.2453 3.68372 16.4112 5.63634 18.3638C7.58896 20.3164 10.7548 20.3164 12.7074 18.3638L14.1216 16.9496L15.5358 18.3638ZM14.8287 7.75717L16.2429 9.17139L9.17187 16.2425L7.75766 14.8282L14.8287 7.75717Z" fill="currentColor"></path></svg>
                                                </span>
                                                <InputField name={"pageUrl"} className={`form-control form-control-lg fw-bolder px-9 ${error ? 'is-invalid' : ''}`} placeholder="Enter Airbnb Page Url" value={pageUrl} onChangeHandler={this.onChangeHandler} />
                                                {error ? <span className="invalid-feedback">This field is required</span> : null}
                                            </div>
                                        </div>
                                        {/* <div className="pb-2">
                                            <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                    <span className="form-check-label">Manually approve posts before making them public?</span>
                                                </CheckBoxField>
                                            </label>
                                        </div> */}
                                    </Tab.Pane>
                                </Tab.Content>
                            </div>
                        </div>
                    </div>
                </div>
            </Tab.Container>
            <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount,
        connectedAccountList: state.addFeed.connectedAcc
    }
}

export default connect(mapStateToProps)(Airbnb)