import React, { useState } from "react";
import PlacesAutocomplete from "react-places-autocomplete";
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Dropdown } from 'react-bootstrap';


export default function PlaceDropDown(props) {
    const [address, setAddress] = useState("");
    const [list, setList] = useState(false);

    const handleChange = (e) => { setAddress(e); setList(true); props.onFieldTextChange("", "") };
    const handleSelect = (e) => { console.log("handleSelect..", e) };

    const handleSelect1 = (v1, v2, v3) => {
        props.onFieldTextChange(v1, v2, v3)
        setAddress(v1);
        setList(false)
    };



    return (
        <PlacesAutocomplete
            value={address}
            onChange={handleChange}
            onSelect={handleSelect}
        >
            {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (

                <div className="position-relative w-100">
                    <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                            <path d="M12 23.7279L5.63604 17.364C2.12132 13.8492 2.12132 8.15076 5.63604 4.63604C9.15076 1.12132 14.8492 1.12132 18.364 4.63604C21.8787 8.15076 21.8787 13.8492 18.364 17.364L12 23.7279ZM16.9497 15.9497C19.6834 13.2161 19.6834 8.78392 16.9497 6.05025C14.2161 3.31658 9.78392 3.31658 7.05025 6.05025C4.31658 8.78392 4.31658 13.2161 7.05025 15.9497L12 20.8995L16.9497 15.9497ZM12 13C10.8954 13 10 12.1046 10 11C10 9.89543 10.8954 9 12 9C13.1046 9 14 9.89543 14 11C14 12.1046 13.1046 13 12 13Z" fill="currentColor"></path>
                        </svg>
                    </span>
                    {loading ? <div class="spinner spinner-gray" style={{top:-22}}></div> : null}
                    <input {...getInputProps({ placeholder: "Type address" })} className={`form-control form-control-lg fw-bolder px-9 ${props.error?'is-invalid':''}`} />
                    {list && address.length > 0 ?
                        <Dropdown className={`w-100`} show>
                            <Dropdown.Menu className="w-100">
                                <PerfectScrollbar options={{ wheelPropagation: false }}>
                                    {suggestions.map(suggestion => {
                                        return (<Dropdown.Item className="text-break" {...getSuggestionItemProps(suggestion)} onClick={() => handleSelect1(suggestion.description, suggestion.placeId, suggestion.formattedSuggestion.mainText)} style={{ whiteSpace: 'normal' }}> {suggestion.description}</Dropdown.Item>);
                                    })}
                                </PerfectScrollbar>
                            </Dropdown.Menu>
                        </Dropdown>
                        : ''}
                </div>
            )}
        </PlacesAutocomplete>
    );
}





