import React, { Component } from 'react';
import { Nav, Tab, Collapse } from "react-bootstrap";
import { capitalizeFirstLetter } from '../../../../Services/Utils/utils';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import InputField from '../Elements/InputField';
import CheckBoxField from '../Elements/CheckboxField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { createFeedAction } from '../../../../Services/Actions/addFeedAction';




class Rss extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            feedUrl: "",
            moderation: false,
            error: false,
            urlError: false,
            loading: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype,
            sampleXml : false
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, urlError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onManuallyApproveChange = (value) => this.setState({ moderation: value })

    onSubmit = () => {
        const { name, feedUrl, moderation, selectedFilterTab ,selectedFilterId} = this.state;
        const { collection, networkId } = this.props;

        if (name == '') {
            this.setState({ error: true });
        } else if (feedUrl == '') {
            this.setState({ urlError: true });
        }
        else {
            var obj = {
                collection: collection != null ? collection.value : "",
                networkId: networkId,
                filter: selectedFilterId,
                moderation: moderation?1:0,
                fieldtitle:name,
            }

            if(!feedUrl.includes("://")){
                this.setState({ urlError: true });
                return;
            }else{
                obj['field'] = feedUrl;
            }

            this.setState({ error: false, urlError: false, loading: true });

            createFeedAction(obj,networkId,() =>{
                this.setState({ loading: false });
                this.props.closeAddFeed();
                this.props.history('/assets');
            });


        }
    }

    feedIcon = (iconId) => {
        switch (iconId) {
            case 18: // Link
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M18.3643 15.5353L16.95 14.1211L18.3643 12.7069C20.3169 10.7543 20.3169 7.58847 18.3643 5.63585C16.4116 3.68323 13.2458 3.68323 11.2932 5.63585L9.87898 7.05007L8.46477 5.63585L9.87898 4.22164C12.6127 1.48797 17.0448 1.48797 19.7785 4.22164C22.5121 6.95531 22.5121 11.3875 19.7785 14.1211L18.3643 15.5353ZM15.5358 18.3638L14.1216 19.778C11.388 22.5117 6.9558 22.5117 4.22213 19.778C1.48846 17.0443 1.48846 12.6122 4.22213 9.87849L5.63634 8.46428L7.05055 9.87849L5.63634 11.2927C3.68372 13.2453 3.68372 16.4112 5.63634 18.3638C7.58896 20.3164 10.7548 20.3164 12.7074 18.3638L14.1216 16.9496L15.5358 18.3638ZM14.8287 7.75717L16.2429 9.17139L9.17187 16.2425L7.75766 14.8282L14.8287 7.75717Z" fill="currentColor"></path></svg>
                break;
            case 16: // Links
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M13 6V21H11V6H5V4H19V6H13Z" fill="currentColor"></path></svg>
                break;
            default:
                break;
        }
    }
    render() {
        const { filterTabs } = this.props;
        const { name, feedUrl, moderation,urlError, error, loading, selectedFilterTab, sampleXml } = this.state
        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                    {filterTabs && Object.keys(filterTabs).length > 1 ?
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">

                                        <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                            {
                                                filterTabs && filterTabs.map((filterTab) => {
                                                    const { feedName, feedtype, filter } = filterTab;
                                                    return <Nav.Item className="p-1" onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                        <Nav.Link eventKey={feedtype}>
                                                            <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                                {this.feedIcon(filter)}
                                                            </span>
                                                            {capitalizeFirstLetter(feedtype)}
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                })
                                            }
                                        </Nav>
                                    </div>
                                </div>
                            </div>
                        </div>
                        :null}
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab} Details</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <Tab.Content>
                                        <Tab.Pane eventKey={"RSS"}>
                                            <div className='row'>
                                                <div className="col-sm-6">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(16)}
                                                        </span>
                                                        <InputField name={"name"} className={`form-control form-control-lg fw-bolder px-9 ${error? 'is-invalid':''}`} placeholder="Display Name" value={name} onChangeHandler={this.onChangeHandler} />
                                                        {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                <div className="col-sm-6">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(18)}
                                                        </span>
                                                        <InputField name={"feedUrl"} className={`form-control form-control-lg fw-bolder px-9 ${urlError? 'is-invalid':''}`} placeholder="Feed URL" value={feedUrl} onChangeHandler={this.onChangeHandler} />
                                                        {urlError ? <span className="invalid-feedback">This field must contain a url</span> : null}
                                                    </div>
                                                </div>
                                            </div>
                                        </Tab.Pane>
                                    </Tab.Content>
                                    <div className="d-flex w-100 justify-content-end mt-2">
                                        <button type="button" className="btn p-0 text-decoration-underline" onClick={()=>this.setState({sampleXml:!sampleXml})}>
                                            <span className="text-right fw-semibold">Download Sample Format</span>
                                        </button>
                                    </div>
                                    <Collapse in={sampleXml}>
                                        <div>
                                            <div className="block-center flex-column m-auto py-3" style={{maxWidth:530}}>
                                                <img className="h-auto w-100" src={baseUrl("media/images/demo/sample-design--xml-rss-feed.png")} height="600" width="300" alt="sample Rss" />
                                                <a href={baseUrl("media/downloads/sample-xml.txt")} target='_blank' className="btn btn-sm btn-secondary" download>
                                                    <span className="svg-icon">
                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                            <path d="M0,0H24V24H0Z" fill="none" />
                                                            <path d="M-945.524-511.2A2.48,2.48,0,0,1-948-513.68v-3.3a.826.826,0,0,1,.825-.826.826.826,0,0,1,.826.826v3.3a.827.827,0,0,0,.826.825h11.56a.827.827,0,0,0,.826-.825v-3.3a.826.826,0,0,1,.825-.826.826.826,0,0,1,.823.826v3.3a2.48,2.48,0,0,1-2.476,2.476Zm5.467-6.3a.809.809,0,0,1-.269-.179l-4.127-4.134a.825.825,0,0,1-.242-.583.826.826,0,0,1,.242-.584.825.825,0,0,1,.583-.242.826.826,0,0,1,.584.242l2.719,2.719v-7.909a.826.826,0,0,1,.826-.826.825.825,0,0,1,.825.826v7.916l2.72-2.719a.824.824,0,0,1,.583-.242.825.825,0,0,1,.583.242.822.822,0,0,1,.242.584.821.821,0,0,1-.242.583l-4.127,4.127a.809.809,0,0,1-.269.179.824.824,0,0,1-.315.065A.826.826,0,0,1-940.057-517.5Z" transform="translate(951.744 532.102)" fill="currentColor" />
                                                        </svg>
                                                    </span>
                                                    Download Sample
                                                </a>
                                            </div>
                                        </div>
                                    </Collapse>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}

export default Rss